import React from 'react';
import { MainContainer } from 'components/MainContainer';
import { Typography } from '@material-ui/core';
import { Form } from 'components/Form';
import { Input } from 'components/Input';
import { PrimaryButton } from 'components/PrimaryButton';
import { useForm } from 'react-hook-form';
import * as yup from 'yup';
import { yupResolver } from '@hookform/resolvers/yup';
import { useData } from 'context/DataContext';

const schema = yup.object().shape({
    firstName: yup
        .string()
        .matches(/^([^0-9]*)$/, '–ò–º—è –Ω–µ –¥–æ–ª–∂–Ω–æ —Å–æ–¥–µ—Ä–∂–∞—Ç—å —Ü–∏—Ñ—Ä—ã')
        .required('–ò–º—è - –æ–±—è–∑–∞—Ç–µ–ª—å–Ω—ã–π –ø–∞—Ä–∞–º–µ—Ç—Ä'),
    lastName: yup
        .string()
        .matches(/^([^0-9]*)$/, '–§–∞–º–∏–ª–∏—è –Ω–µ –¥–æ–ª–∂–Ω–∞ —Å–æ–¥–µ—Ä–∂–∞—Ç—å —Ü–∏—Ñ—Ä—ã')
        .required('–§–∞–º–∏–ª–∏—è - –æ–±—è–∑–∞—Ç–µ–ª—å–Ω—ã–π –ø–∞—Ä–∞–º–µ—Ç—Ä')

});
export const Step1 = ({ history }) => {
    const { data, setValues } = useData(); // –Ω–∞—à –∫–æ–Ω—Ç–µ–∫—Å—Ç —Å–æ —Å—Ç–µ–π—Ç–æ–º
    const {
        register, // ref –∫–æ–Ω—Ç—Ä–æ–ª—å –∑–∞ —Ñ–æ—Ä–º–æ–π
        handleSubmit, // —Ñ—É–Ω–∫—Ü–∏—è –æ–±—Ä–∞–±–æ—Ç–∫–∏ –¥–∞–Ω–Ω—ã—Ö –ø—Ä–∏ —Å–∞–±–º–∏—Ç–µ —Ñ–æ—Ä–º—ã
        errors
    } = useForm({
        mode: 'onBlur', // —ç–ª–µ–º–µ–Ω—Ç –Ω–∞—á–∏–Ω–∞–µ—Ç –≤–∞–ª–∏–¥–∞—Ü–∏—é –ø—Ä–∏ —Ä–∞—Å—Ñ–æ–∫—É—Å–∏—Ä–æ–≤–∫–µ
        resolver: yupResolver(schema),
        defaultValues: { firstName: data.firstName, lastName: data.lastName }
    });
    const onSubmit = (values) => {
        setValues(values); // –¥–∞–Ω–Ω—ã–µ —Å–æ—Ö—Ä–∞–Ω—è–µ–º –≤ –∫–æ–Ω—Ç–µ–∫—Å—Ç –Ω–∞ –∫–∞–∂–¥–æ–º —à–∞–≥–µ
        history.push('/step2');
    };
    return (
        <MainContainer>
            {' '}
            {/* MainContainer ‚Äî –æ–∫–æ—à–∫–æ –ø–∞–Ω–µ–ª—å–∫–∞ –Ω–∞ –∫–æ—Ç–æ—Ä–æ–π –≤—Å–µ –±—É–¥–µ—Ç —Ä–µ–Ω–¥–µ—Ä–∏—Ç—å—Å—è,
         –ø—Ä—è–º–æ—É–≥–æ–ª—å–Ω–∏–∫ —Å–æ —Å–∫—Ä—É–≥–µ–ª–Ω–Ω—ã–º–∏ –ø–æ–ª—è–º–∏ –∏ —Ç–µ–Ω—å—é */ }
            <Typography component="h2" variant="h5">–®–∞–≥ 1</Typography>
            <Form onSubmit={handleSubmit(onSubmit)}>
                <Input
                    ref={register}
                    id="firstName"
                    type="text"
                    label="–ò–º—è"
                    name="firstName" // –∏–º—è –ø–æ–ª—è –¥–∞–Ω–Ω—ã—Ö –≤ –æ–±—ä–µ–∫—Ç–µ handleSubmit
                    error={!!errors.firstName}
                    helperText={errors?.firstName?.message}
                />
                <Input
                    ref={register}
                    id="lastName"
                    type="text"
                    label="–§–∞–º–∏–ª–∏—è"
                    name="lastName" // –∏–º—è –ø–æ–ª—è –¥–∞–Ω–Ω—ã—Ö –≤ –æ–±—ä–µ–∫—Ç–µ handleSubmit
                    error={!!errors.lastName}
                    helperText={errors?.lastName?.message}
                />
                <PrimaryButton>–í–ø–µ—Ä–µ–¥</PrimaryButton>
            </Form>

        </MainContainer>
    );
};
