import React from 'react';
import { MainContainer } from 'components/MainContainer';
import { Typography } from '@material-ui/core';
import { Form } from 'components/Form';
import { Input } from 'components/Input';
import { PrimaryButton } from 'components/PrimaryButton';
import { useForm } from 'react-hook-form';
import * as yup from 'yup';
import { yupResolver } from '@hookform/resolvers/yup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import { parsePhoneNumberFromString } from 'libphonenumber-js';
import { useData } from 'context/DataContext';

const schema = yup.object().shape({
    email: yup
        .string()
        .email('Email –¥–æ–ª–∂–µ–Ω –±—ã—Ç—å –∫–æ—Ä—Ä–µ–∫—Ç–Ω–æ–≥–æ —Ñ–æ—Ä–º–∞—Ç–∞')
        .required('Email - –æ–±—è–∑–∞—Ç–µ–ª—å–Ω—ã–π –ø–∞—Ä–∞–º–µ—Ç—Ä')

});

const normalizePhoneNumber = (value) => {
    const phoneNumber = parsePhoneNumberFromString(value);
    if (!phoneNumber) return value;
    return phoneNumber.formatInternational();
    // return phoneNumber.formatNational()
};

export const Step2 = ({ history }) => {
    const { data, setValues } = useData(); // –Ω–∞—à –∫–æ–Ω—Ç–µ–∫—Å—Ç
    const {
        register, // ref –∫–æ–Ω—Ç—Ä–æ–ª—å –∑–∞ —Ñ–æ—Ä–º–æ–π
        handleSubmit, // —Ñ—É–Ω–∫—Ü–∏—è –æ–±—Ä–∞–±–æ—Ç–∫–∏ –¥–∞–Ω–Ω—ã—Ö –ø—Ä–∏ —Å–∞–±–º–∏—Ç–µ —Ñ–æ—Ä–º—ã
        errors,
        watch
    } = useForm({
        mode: 'onBlur', // —ç–ª–µ–º–µ–Ω—Ç –Ω–∞—á–∏–Ω–∞–µ—Ç –≤–∞–ª–∏–¥–∞—Ü–∏—é –ø—Ä–∏ —Ä–∞—Å—Ñ–æ–∫—É—Å–∏—Ä–æ–≤–∫–µ
        resolver: yupResolver(schema),
        defaultValues: { email: data.email, phoneNumber: data.phoneNumber, hasPhone: data.hasPhone }
    });
    const onSubmit = (values) => {
        setValues(values); // –¥–∞–Ω–Ω—ã–µ —Å–æ—Ö—Ä–∞–Ω—è–µ–º –≤ –∫–æ–Ω—Ç–µ–∫—Å—Ç –Ω–∞ –∫–∞–∂–¥–æ–º —à–∞–≥–µ
        history.push('/step3');
    };
    const hasPhone = watch('hasPhone');
    return (
        <MainContainer>
            {' '}
            {/* MainContainer ‚Äî –æ–∫–æ—à–∫–æ –ø–∞–Ω–µ–ª—å–∫–∞ –Ω–∞ –∫–æ—Ç–æ—Ä–æ–π –≤—Å–µ –±—É–¥–µ—Ç —Ä–µ–Ω–¥–µ—Ä–∏—Ç—å—Å—è,
         –ø—Ä—è–º–æ—É–≥–æ–ª—å–Ω–∏–∫ —Å–æ —Å–∫—Ä—É–≥–µ–ª–Ω–Ω—ã–º–∏ –ø–æ–ª—è–º–∏ –∏ —Ç–µ–Ω—å—é */ }
            <Typography component="h2" variant="h5">–®–∞–≥ 2</Typography>
            <Form onSubmit={handleSubmit(onSubmit)}>
                <Input
                    ref={register}
                    id="email"
                    type="email"
                    label="Email"
                    name="email" // –∏–º—è –ø–æ–ª—è –¥–∞–Ω–Ω—ã—Ö –≤ –æ–±—ä–µ–∫—Ç–µ handleSubmit
                    required
                    error={!!errors.email}
                    helperText={errors?.email?.message}
                />
                <FormControlLabel
                    control={(
                        <Checkbox
                            name="hasPhone"
                            defaultValue={data.hasPhone}
                            defaultChecked={data.hasPhone}
                            inputRef={register}
                            color="primary"
                        />
                    )}
                    label="–£ –≤–∞—Å –µ—Å—Ç—å –Ω–æ–º–µ—Ä —Ç–µ–ª–µ—Ñ–æ–Ω–∞?"
                />
                {
                    hasPhone && (
                        <Input
                            ref={register}
                            id="phoneNumber"
                            type="tel"
                            label="–¢–µ–ª–µ—Ñ–æ–Ω"
                            name="phoneNumber"
                            onChange={(event) => {
                                event.target.value = normalizePhoneNumber(event.target.value);
                            }}
                        />
                    )
                }
                <PrimaryButton>–î–∞–ª—å—à–µ</PrimaryButton>
                <PrimaryButton
                    type="button"
                    onClick={() => history.push('/')}
                    color="secondary"
                >
                    –ù–∞–∑–∞–¥
                </PrimaryButton>
            </Form>

        </MainContainer>
    );
};
