import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { Container } from '@material-ui/core';

// –∫–∞—Å—Ç–æ–º–Ω—ã–π —Ö—É–∫ –¥–ª—è –∏—Å–ø–æ–ª—å–∑–æ–≤–∞–Ω–∏—è —Å—Ç–∏–ª–µ–π
const useStyles = makeStyles((theme) => ({
    root: {
        marginTop: theme.spacing(4),
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
    }
}));

// MainContainer ‚Äî –æ–∫–æ—à–∫–æ –ø–∞–Ω–µ–ª—å–∫–∞ –Ω–∞ –∫–æ—Ç–æ—Ä–æ–π –≤—Å–µ –±—É–¥–µ—Ç —Ä–µ–Ω–¥–µ—Ä–∏—Ç—å—Å—è ‚Äî –ø—Ä—è–º–æ—É–≥–æ–ª—å–Ω–∏–∫ —Å–æ —Å–∫—Ä—É–≥–µ–ª–Ω–Ω—ã–º–∏ –ø–æ–ª—è–º–∏ –∏ —Ç–µ–Ω—å—é
// props –¥–ª—è –ø–µ—Ä–µ–æ–ø—Ä–µ–¥–µ–ª–µ–Ω–∏—è —á–µ–≥–æ –Ω–∏–¥—å –≤ MainContainer
export const MainContainer = ({children, ...props}) => {
        const styles = useStyles();
        return (
            <Container className={styles.root} container={'main'} maxWidth={'xs'} {...props}>
                { children }
            </Container>
        );
    }
;

